        </div>
        <div id="satelliteMap" class="map map-big"></div>
      </card>
    </div>
    <div class="col-md-6">
      <card class="card-map">
        <div slot="header">
          <h4 class="card-title">Phân Hiệu Đại Học Thủy Lợi</h4>
        </div>
        <div id="initPHTLMap" class="map"></div>
      </card>
